.webmain .con .title h2 {
  background: url("../../../images/sprite_actorinfo.gif") 0 0 no-repeat; }

.webmain .topcon .leftsection {
  width: 660px;
  float: left; }
  .webmain .topcon .leftsection h3 {
    font-size: 40px;
    line-height: 90px; }
  .webmain .topcon .leftsection span {
    font-size: 14px;
    line-height: 35px; }
  .webmain .topcon .leftsection ul {
    margin-left: 75px; }
    .webmain .topcon .leftsection ul .title {
      position: absolute;
      margin-left: -75px;
      color: #ccc; }

.webmain .topcon .rightsection {
  margin-left: 740px; }
  .webmain .topcon .rightsection img {
    width: 425px;
    height: 415px; }
  .webmain .topcon .rightsection span {
    position: absolute;
    height: 20px;
    margin-left: 10px;
    margin-top: -30px;
    font-size: 18px; }
  .webmain .topcon .rightsection a {
    position: absolute;
    width: 155px;
    height: 44px;
    margin-left: 270px;
    margin-top: -44px;
    text-align: center;
    line-height: 44px;
    color: #fff;
    background: #000; }

.webmain .work .banner {
  position: relative;
  height: 200px; }
  .webmain .work .banner .pre {
    position: absolute;
    left: 0;
    z-index: 1;
    width: 45px;
    height: 200px;
    background: url("../../../images/sprite_performce.png") 0 0 no-repeat; }
  .webmain .work .banner .next {
    position: absolute;
    right: 0;
    z-index: 1;
    width: 45px;
    height: 200px;
    background: url("../../../images/sprite_performce.png") 0 -200px no-repeat; }
  .webmain .work .banner ul {
    position: absolute;
    left: 0;
    width: 2000px; }
    .webmain .work .banner ul li {
      float: left;
      width: 175px;
      height: 200px; }
      .webmain .work .banner ul li + li {
        margin-left: 30px; }
      .webmain .work .banner ul li img {
        width: 175px;
        height: 200px; }
      .webmain .work .banner ul li p {
        height: 30px;
        margin-top: -30px;
        line-height: 30px;
        text-align: center;
        color: #fff;
        background: url("../../../images/space.png") 0 0 repeat;
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000,endColorstr=#99000000);
        background: rgba(0, 0, 0, 0.6); }

.webmain .newstatus {
  position: relative;
  float: left;
  width: 790px; }
  .webmain .newstatus .title h2 {
    background-position: 0 -60px; }
  .webmain .newstatus .item {
    height: 160px;
    margin-bottom: 30px; }
    .webmain .newstatus .item img {
      float: left;
      width: 240px;
      height: 160px; }
    .webmain .newstatus .item .content {
      margin-left: 270px; }
      .webmain .newstatus .item .content h3 {
        font-size: 18px;
        line-height: 30px;
        margin-bottom: 30px; }
      .webmain .newstatus .item .content span {
        display: block;
        font-size: 16px;
        color: #ccc; }
      .webmain .newstatus .item .content .more {
        float: right; }

.webmain .fansactive {
  margin-left: 820px; }
  .webmain .fansactive .title h2 {
    background-position: 0 -120px; }
  .webmain .fansactive .item img, .webmain .fansactive .item sub {
    width: 380px;
    height: 280px; }
  .webmain .fansactive .item .text {
    position: absolute;
    width: 380px;
    height: 65px;
    margin-top: -65px;
    color: #fff;
    text-indent: 20px;
    background: url("../../../images/space.png") 0 0 repeat;
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000,endColorstr=#99000000);
    background: rgba(0, 0, 0, 0.6); }
    .webmain .fansactive .item .text h1 {
      font-size: 14px;
      line-height: 32.5px; }
    .webmain .fansactive .item .text h3 {
      font-size: 12px;
      line-height: 32.5px; }
  .webmain .fansactive .item sub {
    position: absolute;
    margin-top: -280px;
    background: transparent;
    background: url("../../../images/space.png") 0 0 repeat; }
    .webmain .fansactive .item sub:hover {
      background: rgba(0, 0, 0, 0.3);
      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#4c000000,endColorstr=#4c000000); }
  .webmain .fansactive .intro {
    width: 380px; }
    .webmain .fansactive .intro p {
      font-size: 12px;
      line-height: 30px; }
    .webmain .fansactive .intro .focus {
      float: right;
      width: 30px;
      height: 30px;
      background: url("../../../images/icon_focus.jpg"); }
    .webmain .fansactive .intro .num {
      float: right;
      height: 30px;
      font-size: 12px;
      line-height: 30px;
      color: #ccc; }
